import { data } from "./array";
import Book from "./eachBook";
import Nav from "./nav";

export default function Buybooks() {
    
    
    return <div className="buy">
          <Nav />
        <div className="pbook">
            {
                data.map(obj => {
                    return <Book url={obj.url} name={obj.name} key={obj.id} amount={obj.amount} id={obj.id} />
                })
            }
        </div>
    </div>
}