import {BrowserRouter,Routes,Route} from 'react-router-dom'
import Inup from './Inup'
import Inside from './inside'
import Buybooks from './buybooks'
import FinalBook from './finalBook'
import SelectedBook from './selectedbook'
import Book1 from './book1'

export default function(){
 return <BrowserRouter>
 <Routes>
    <Route element={<Inup />} path='/'></Route>
    <Route element={<Inside />} path='/in/:name'></Route>
    <Route element={<Buybooks />} path='/buy'></Route>
    <Route element={<SelectedBook />} path='/selectedbook/:id'></Route>
    <Route element={<FinalBook />} path='/payment/:id'></Route>
    <Route element={<Book1 />} path='/search/:id'></Route>
 </Routes> 
 </BrowserRouter>
}