import { useEffect, useState } from "react";
import axios from "axios"
import { useNavigate } from "react-router-dom/dist/umd/react-router-dom.development";
export default function Signup() {
    const [data,setData] = useState({});
    useEffect(() => {
        axios.get("https://6499776d79fbe9bcf83f4d7a.mockapi.io/sign_up").then(res => {
            setData(res.data);
        })
    },[])
    const history = useNavigate();
    var obj = {
        fname: null,
        sname: null,
        phone: null,
        email: null,
        pass: null,
        cpass: null,
        pa: null,
        ca: "" ,
        profile : null
    };
    const [sd, setSd] = useState(obj)
    const handleProfileImage = (e) => {
        var str = e.target.value;
        var array = str.split("\\");
        var num = array.length;
        setSd({ ...sd,profile : array[num - 1]})
        setTimeout(() => {
            console.log(sd)
        },5000)
    }
    const handleChange = (e) => {
         if(e.target.name === 'check'){
              if(e.target.checked){
                var val = sd.pa;
                setSd({...sd,ca:val})
              }else{
                console.log("false")
                setSd({...sd,ca:''})
              }
         }else{
             setSd({ ...sd, [e.target.name]: e.target.value })
         }
    }
    const handleClick = () => {
        if (sd.fname === null || sd.sname === null || sd.email === null || sd.pass === null || sd.cpass === null || sd.phone === null || sd.pa === null || sd.ca === null || sd.ca === "") {
            alert("Please First Fill All Details.!");
        } else{
            var bool = true;
            data.forEach(item => {
                if(item.email === sd.email){
                    alert("This Email already have an account!");
                    bool = false;
                }else if(item.phone === sd.phone){
                    alert("mobile number already in use!");
                    bool = false;
                }
            });
           if(bool){
               axios.post("https://6499776d79fbe9bcf83f4d7a.mockapi.io/sign_up", sd).then(res => {
                   history(`/in/${sd.profile}`);
               })
           }
        }
    }
    return <div data-aos="zoom-in" data-aos-duration="500" className='log' id="signup">
        <div className="flex topdiv align-center">
            <div className="flex align-center justify-center">
                <div className='div-content h'>
                    <h3>Sign Up</h3>
                    <div className="flex justify-between">
                        <div>
                            <div className="flex srt">
                                <div>
                                    <label>Firstname</label><br></br>
                                    <input type="text" placeholder="Vipul" name="fname" onChange={e => handleChange(e)} required />
                                </div>
                                <div>
                                    <label>Surname</label><br></br>
                                    <input type="text" placeholder="Purohit" name="sname" onChange={e => handleChange(e)} required />
                                </div>
                            </div>
                            <div className="flex srt">
                                <div>
                                    <label>Email Address</label><br></br>
                                    <input type="text" placeholder="vip12@gmail.com" name="email" onChange={e => handleChange(e)} required />
                                </div>
                                <div>
                                    <label>Contact</label><br></br>
                                    <input type="text" placeholder="9313793086" name="phone" onChange={e => handleChange(e)} required />
                                </div>
                            </div>
                            <div className="flex srt">
                                <div>
                                    <label>Password</label><br></br>
                                    <input type="text" placeholder="Pvv@1012" name="pass" onChange={e => handleChange(e)} required />
                                </div>
                                <div>
                                    <label>Confirm Password</label><br></br>
                                    <input type="text" placeholder="Pvv@1012" name="cpass" onChange={e => handleChange(e)} required /><br></br>
                                </div>
                            </div>
                            <label>Permanent Address</label><br></br>
                            <input type="text" name="pa" onChange={e => handleChange(e)} required /><br></br>
                            <div className="flex equal">
                                <input type="checkbox" name="check" onChange={e => handleChange(e)} />
                                <p>same as a Permanent Address</p>
                            </div>
                            <label>Currrent Address</label><br></br>
                            <input type="text" name="ca" value={sd.ca} onChange={e => handleChange(e)} required /><br></br>
                            <form action="/uploads" method="post" encType="multipart/form-data">
                            <label>Select Profile Picture</label>
                            <input type="file" name="photo" onChange={e => handleProfileImage(e)}/>
                            <button type="submit">Upload</button>
                            </form>
                            <p>Does have an account yet? click on Login</p>
                            <button type="button" onClick={handleClick}>Sign Up</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
}
