import { useNavigate, useParams } from "react-router-dom/dist/umd/react-router-dom.development"
import Nav from "./nav"
import { data } from "./array";
import { useEffect, useState } from "react";
export default function Book1() {
    var prevObj =  { url: null, name: null, amount: null, id: null };
    const [obj, setObj] = useState(prevObj);
    const { id } = useParams();

    useEffect(() => {
        for (var i = 0; i < data.length; i++) {
            if (Number(data[i].id) === Number(id)) {
                setObj({
                    ...prevObj,
                    url : data[i].url,
                    name : data[i].name,
                    amount : data[i].amount,
                    id : data[i].id
                })
            }
        }
    },[])

    const history = useNavigate()
    function handleClick(id) {
        history(`/selectedbook/${id}`)
    }

    return <div className="buy">
        <Nav />
        <div className="pbook">
        <div className="bbook">
            <img src={obj.url} width="160px" height="240px" />
            <pre>Buy Books for your great</pre><pre>improvement and mindset</pre>
            <p>{obj.amount} ₹</p>
            <button className="b1btn w8" type="text" onClick={handleClick(obj.id)}>Buy</button>
        </div>
        </div>
    </div>
}