import { useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom/dist/umd/react-router-dom.development";
import { data } from "./array";
// import Razorpay from 'razorpay'
import Nav from "./nav";
export default function FinalBook() {
    const [hasclassName, setHasclassName] = useState(true)
    const [add,setAdd] = useState(null)
    const history = useNavigate()
    var prevObj = { url: null, name: null, amount: null, id: null };
    const [obj, setObj] = useState(prevObj);
    const { id } = useParams();

    useEffect(() => {
        for (var i = 0; i < data.length; i++) {
            if (Number(data[i].id) === Number(id)) {
                setObj({
                    ...prevObj,
                    url: data[i].url,
                    name: data[i].name,
                    amount: data[i].amount,
                    id: data[i].id
                })
            }
        }
    }, [])
    const handleBack = () => {
        history(-1)
    }
    const confirmBox = () => {
        if (hasclassName) {
            setHasclassName(false)
        }
    }
    const handleNext = () => {
        if (!hasclassName && add) {
            setHasclassName(true);
            handlePay(obj.amount)
        }else{
            alert('Plz Select The Dispatch Address')
        }
    }


    const handlePay = (amount) => {
        fetch('/pay', {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({ amount: amount, currency: 'INR' })
        }).then(response => response.json())
            .then(data => {
                const options = {
                    key: 'rzp_test_06I3Zl1gQohVlT',
                    amount: Number(data.amount * 100),
                    currency: data.currency,
                    name: 'Vipul Purohit',
                    description: 'Payment description',
                    image: 'https://lh3.googleusercontent.com/ogw/AOLn63GD7rs-7zmWTM9AXuOKpClAmH6T5ZEYjQcrL_az=s32-c-mo',
                    order_id: data.id,
                    handler: function (response) {
                        console.log('Success')
                    },
                    prefill: {
                        name: 'vipul purohit',
                        email: 'vipulpurohit52@gmail.com',
                        contact: `+919313793086`
                    }
                };

                const rzp = new window.Razorpay(options);
                rzp.open();
            })
            .catch(error => {
                console.error('Error:', error);
               
            });
    }
    const handlechange = (e) => {
        if(e.target.checked){
            setAdd(e.target.value)
        }
    }


return <div className="buy">
    <Nav />
    <div className="flex align-center justify-center p2book append">
        <div className="dv1">
            <img src={obj.url} width="350px" height="450px" />
        </div>
        <div className="detail">
            <h1 className="h12">{obj.name}</h1>
            <p className="p12">This is the one of the best book for our self improvement , our life motivation , improves brain connectivity , increases your vocabulary and comprehension , reduces stress and aids in sleep readiness.</p>
            <div className="back">4.8 ⭐ (1024)</div>
            <pre className="pre">Get it by Monday, 3 July 2023 </pre>
            <pre className="pre1">FREE Delivery by Our Online Library </pre>
            <div className="flex align-center ">
                <h1 className="ml-4 h13">  ₹{obj.amount} </h1><p className="ml-4 p13" >₹{obj.amount + 130}</p><span className="span">56% off</span>
            </div>
            <div className="flex">
                <button className="b1btn bb2"  onClick={e => confirmBox()}> Pay </button>
                <button className="b1btn bb2" onClick={handleBack}> Back </button>
            </div></div>
    
    <div className={hasclassName ? "confirm1 hidden" : "confirm1"}>
        <div className="confirm2">
            <div className="title"> Dispatch Details </div>
            <div className="body">On Which Location We Dispatch Your Book!</div>
            <div className="flex  align-center">
                <input className="incheck" type="radio" name="gender" value="ca" onChange={e => handlechange(e)}/><label>Current Address</label>
                <input className="incheck" type="radio" name="gender" value="pa" onChange={e => handlechange(e)} /><label>Permanant Address</label>
            </div>
            <button type="button" onClick={handleNext}>Next</button>
            <button type="button" onClick={e => {if(!hasclassName){setHasclassName(true)}}}>Cancel</button>
        </div>
    </div>
</div>
</div>
}