import React, { useEffect, useState } from "react";
import { data } from "./array";
import { useNavigate } from "react-router-dom/dist/umd/react-router-dom.development";

export default function Nav() {
    const history = useNavigate();
    const handleFocus = () => {
        var searchInput = document.getElementById('search-result');
        var resultsList = document.getElementById('results');
        data.forEach((result, i) => {
            const listItem = document.createElement('li');
            listItem.addEventListener('click', (e) => {
                searchInput.value = listItem.innerText;
                resultsList.innerHTML = '';
                handleicon(listItem.innerText);
            })
            listItem.textContent = result.name;
            resultsList.insertAdjacentElement('beforeend', listItem);
        });
    }
    const handleBlur = () => {
        var resultsList = document.getElementById('results');
        setTimeout(() => {
            resultsList.innerHTML = null;
        }, 500)
    }
    useEffect(() => {
        var searchInput = document.getElementById('search-result');
        var resultsList = document.getElementById('results');
        searchInput.addEventListener('input', (e) => {
            if (e.target.value === ' ' || e.target.value === '') {
                resultsList.innerHTML = '';
            } else {
                var searchTerm = e.target.value.toLowerCase();
                resultsList.innerHTML = '';
                var results;
                results = performSearch(searchTerm);
                // Display results
                results.forEach((result, i) => {
                    const listItem = document.createElement('li');
                    listItem.addEventListener('click', (e) => {
                        searchInput.value = listItem.innerText;
                        resultsList.innerHTML = '';
                        handleicon(listItem.innerText);
                    })
                    listItem.textContent = result;
                    resultsList.appendChild(listItem);
                });
            }
        });

        function performSearch(searchTerm) {
            // Simulating a search function
            var data1 = [];
            data.map(obj => {
                data1.push(obj.name);
            })
            return data1.filter(item => item.toLowerCase().includes(searchTerm));
        }
    })

    function handleicon(title) {
        var id;
        data.map(obj => {
            if (obj.name === title) {
                id = obj.id;
            }
        })
        history(`/search/${id}`)
    }

    return <><div className='inup-nav'>
        <nav className='container flex justify-between align-center'>
            <div className='flex align-center'>
                <img className="l1" src='../icons/Capturemlk.png' width='70px' height='50px' />
                <h2 className='logo'>LIBRARY</h2>
            </div>
            <div className="searchbar flex align-center justify-center">
                <input type="text" id="search-result" onFocus={handleFocus} onBlur={handleBlur} placeholder="search books here.." />
                <img src="../icons/search (1).png" width="30px" height="30px" />
            </div>
            <a className="back" href="/in">Home</a>
        </nav>
    </div>
        <ul id="results"></ul>
    </>
}