import { useNavigate } from "react-router-dom/dist/umd/react-router-dom.development"
export default function Book({ url, amount, name, id }) {
    const history = useNavigate()
     function handleClick (id){
           history(`/payment/${id}`)
     }

    return <div className="bbook">
        <img src={url} width="160px" height="240px" />
        <pre>Buy Books for your great</pre><pre>improvement and mindset</pre>
        <p>{amount} ₹</p>
        <button className="b1btn w8" type="text" onClick={() => handleClick(id)}>Buy</button>
    </div>
}