export default function Bk({url,func,num}){
   return  <div className="bk align-center">
   <img src={url} width="160px" height="230px" />
   <h4 onClick={() => func(num)}>Read</h4>
</div>
}

export const Bk1 = ({img,target}) => {
    return  <div className="add">
    <img src={img} width="160px" height="230px" />
    <a href={target} target="_blank" >Listen</a>
</div>
}