import { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom/dist/umd/react-router-dom.development";


export default function Login(){
    const [arr,setArr] = useState(null);
    const history = useNavigate();
    useEffect(() => {
     axios.get("https://6499776d79fbe9bcf83f4d7a.mockapi.io/sign_up").then(res => {
        setArr(res.data)
     })
    },[])
    var obj = {
        ename : null,
        pass : null
    };
    const [ld,setLd] = useState(obj);
    const handleChange = (e) =>{
        setLd({...ld,[e.target.name] : e.target.value})
    }
    const handleClick = () => {
    var i = 0;
    for(var j = 0;j < arr.length;j++){
        if(ld.ename === arr[j].email && ld.pass === arr[j].pass){
            console.log(arr[j])
            history(`/in/${arr[j].profile}`);
            i++;
        }
    }
        if(i === 0){
            alert('user not found! keep first sign up.');
            setLd(obj)
        }
    }
        return <div data-aos="zoom-in" data-aos-duration="500" id="login" className='log  align-center'>
        <div className=" topdiv align-center">
            <div className=" align-center justify-center">
                <div className='div-content h1'>      
                    <div>
                    <h3>Login</h3>
                    <label>Email Address</label><br></br>
                    <input type="text" placeholder="vip12@gmail.com" name="ename" value={ld.ename} onChange={e => handleChange(e)} required/><br></br>
                    <label>Password</label><br></br>
                     <input type="text" placeholder="Pvv@1012" name="pass" value={ld.pass} onChange={e => handleChange(e)} required/><br></br>
                    <p>Doesn't have an account yet? click on Sign up</p>
                     <div className="flex">
                     <button type="button" onClick={handleClick}>Login</button>
                     </div>
                     </div>
                </div>
            </div>
        </div>
    </div>
}